import React, { useState } from "react";
import {
    withGoogleMap,
    withScriptjs,
    GoogleMap,
    Marker,
    InfoWindow
} from "react-google-maps";

function Map(props) {
    const [selectedSpot, setSelectedSpot] = useState(null);
    const dataResponse = props.dataResponse
    const filterData = props.filterData
    let spots = []
    let validSpots = []
    let filterSpots = []


    if (dataResponse && dataResponse.data) {
        spots = dataResponse.data.data.spots
        spots.map(spot => {
            if (spot.zip_code.city === "Ciudad de MÃ©xico" && spot.is_public !== 0) {
                validSpots.push(spot)
            }
        })
    }

    if (filterData) {
        validSpots.map(validSpot => {
            if (validSpot.spot_type_id === filterData.type ||
                validSpot.term === filterData.term ||
                validSpot.square_space === filterData.square_space) {
                filterSpots.push(validSpot)
            }
        })
        validSpots = filterSpots
    }

    return (
        <GoogleMap
            defaultZoom={11}
            defaultCenter={{ lat: 19.42847, lng: -99.12766 }}
        >
            {validSpots.map((spot) => (
                <Marker
                    key={spot.id}
                    position={{
                        lat: spot.location.coordinates[1],
                        lng: spot.location.coordinates[0]
                    }}
                    onClick={() => {
                        setSelectedSpot(spot);
                    }}
                />
            ))}

            {selectedSpot && (
                <InfoWindow
                    onCloseClick={() => {
                        setSelectedSpot(null);
                    }}
                    position={{
                        lat: selectedSpot.location.coordinates[1],
                        lng: selectedSpot.location.coordinates[0]
                    }}
                >
                    <div>
                        <h4>{selectedSpot.name}</h4>
                        <hr></hr>
                        <p className="map-text">{selectedSpot.description}</p>
                    </div>
                </InfoWindow>
            )}
        </GoogleMap>
    );
}

const MapComponent = withScriptjs(withGoogleMap(Map));

export default MapComponent