import React, { useState } from 'react';
import '../css/styles.css';

export default function RangeValue(props) {
    const [rangevalue, setRangevalue] = useState(null);

    const handleChange = (e) => {
        setRangevalue(e.target.value);
        props.getRangeValue(parseInt(e.target.value))
    }

    return (
        <div className="container-fluid container-range">
            <label htmlFor="customRange" className="form-label">Size {rangevalue} mts</label>
            <input type="range" className="custom-range" min="0" max="3" step="1" defaultValue="0" onChange={handleChange} list="tickmarks" />
        </div>
    )
}

