import React, { useState } from 'react';

export default function TypeSpot(props) {
    const types = [
        {
            value: 0,
            name: 'Type of Spot'
        },
        {
            value: 1,
            name: 'Street Spot'
        },
        {
            value: 2,
            name: 'In mall Spot'
        },
        {
            value: 3,
            name: 'In mall island'
        },
        {
            value: 4,
            name: 'Other'
        },
    ];

    const [value, setValue] = useState(0)

    const handleChange = (e) => {
        setValue(e.target.value);
        props.getTypeSpot(parseInt(e.target.value));
    }

    return (
        <div className="container-fluid container-selects">
            <select className="form-select form-select-sm" aria-label=".form-select-sm example" value={value} onChange={handleChange} >
                {types.map(type => <option key={type.value} value={type.value}>{type.name}</option>)}
            </select>
        </div>
    )
}

