import React, { Component } from 'react';
import axios from 'axios'
import 'bootstrap/dist/css/bootstrap.min.css'
import '../css/styles.css';

import TypeSpot from '../Containers/TypeSpot';
import TypeRent from '../Containers/TypeRent';
import RageValue from '../Containers/RangeValue';
import ButtonFilter from '../Containers/ButtonFilter';

class SearchComponent extends Component {
    state = {
        response: null,
        params: {},
        disabled: true,
    }

    componentDidMount() {
        axios.get(`https://staging.spot2.mx/api/spots`,
            { headers: { Authorization: `Bearer 18|YEAJ595dvUKaeFDljCPZ9LSPFMNhCpIClwk7iwmA` } }
        )
            .then(response => {
                this.setState({
                    response: response
                })
                this.props.handleResponse(response)
            })

            .catch(error => {
                console.log(error);
            })
    }

    getTypeSpot = (value, params) => {
        this.setState({
            params: {
                type: value
            },
            disabled: false
        })
        this.props.handleFilter(params)
    }

    getTypeRent = (value, params) => {
        this.setState({
            params: {
                type: this.state.params.type,
                term: value
            },
            disabled: false
        })
        this.props.handleFilter(params)
    }

    getRangeValue = (value, params) => {
        this.setState({
            params: {
                type: this.state.params.type,
                term: this.state.params.term,
                square_space: value,
            },
            disabled: false,
        })
        this.props.handleFilter(params)
    }

    getSearchRequest = () => {
        const params = this.state.params
        this.props.handleFilter(params)
    }

    render() {
        const { disabled } = this.state

        return (
            <div className="search container-fluid">
                <TypeSpot getTypeSpot={this.getTypeSpot} />
                <TypeRent getTypeRent={this.getTypeRent} />
                <RageValue getRangeValue={this.getRangeValue} />
                <ButtonFilter getSearchRequest={this.getSearchRequest} disabled={disabled} />
            </div>
        )
    }
}

export default SearchComponent